@use "../../styles/typography.scss" as *;
@use "../../styles/variables" as *;


.skills {
    padding: 1rem 2rem 2rem 2rem;
    font-family: $font-heading;
    animation: skills 1s ease-in-out 1;
    &__heading {
        margin-left: 10vw;
        color: $color-main;
    }

    &__languages {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: 0 10vw;

        &--heading {
            color: $color-main;
            font-size: 1rem;
            font-weight: 400;
            text-align: center;
        }

        &--image {
            width: 3rem;
        }
    }

    &__language {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        height: 5rem;
        margin: 2rem 1rem;
    }

    @keyframes skills {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
}

@media screen and (max-width: 900px) {
    .skills {
        padding: 1rem 2rem 2rem 2rem;
        font-family: $font-heading;
        animation: skills 1s ease-in-out 1;
        
        &__heading {
            margin-left: 10vw;
            color: $color-main;
            font-size: 1.5rem;
            margin-top: 1rem;
        }
    
        &__languages {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
    
            &--heading {
                color: $color-main;
                font-size: 1rem;
                font-weight: 400;
                text-align: center;
            }
    
            &--image {
                width: 2rem;
            }
        }
    
        &__language {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            height: 4rem;
            width: 4rem;
            margin: 1rem 0.5rem;
        }
    
        @keyframes skills {
            from {
                opacity: 0;
            }
            to {
                opacity: 1;
            }
        }
    }
}